
import React, { Component } from 'react';
import { Modal }  from 'react-bootstrap';
import { baseUrl } from '../../../../../Assets/AssetsHelper';

class AddNewCard extends Component {
    constructor(props) {
        super(props);
            this.state = {
        };
    }

    handleClose = () => {
        this.props.viewInvoiceCallback(false)
    };

    render() {
        const {modalToggle} = this.props
        return <Modal show={modalToggle} onHide={this.handleClose}
                aria-labelledby="contained-modal-title-vcenter"
                centered
                >
                    <Modal.Header closeButton>
                        <Modal.Title>Add New Card</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <div className="px-xl-5">
                            <div className="row">
                                <div className="col-sm-12 mb-3 mb-xl-4">
                                    <label className="fw-semibold text-gray-500 mb-2">Name on Card <strong className="text-danger">*</strong></label>
                                    <input type="text" name="fname" className="form-control mb-3 mb-sm-0 is-valid" placeholder="First name" defaultValue="Ramona"/>
                                </div>

                                <div className="col-sm-12 mb-3 mb-xl-4">
                                    <label className="fw-semibold text-gray-500 mb-2">Card Number <strong className="text-danger">*</strong></label>
                                    <div className="position-relative">
                                        <input type="text" className="form-control" placeholder="Enter card number" name="card_number" defaultValue="4111 1111 1111 1111"/>
                                        <div className="position-absolute translate-middle-y top-50 end-0 me-2">
                                            <img className="h-24px me-1" src={baseUrl("media/images/payment/visa.svg")} height={25} width={25} alt="visa" />
                                            <img className="h-24px me-1" src={baseUrl("media/images/payment/mastercard.svg")} height={25} width={25} alt="visa" />
                                            <img className="h-24px" src={baseUrl("media/images/payment/american-express.svg")} height={25} width={25} alt="visa" />
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div className="row mb-4 mb-xl-5">
                                <div className="col-sm-8">
                                    <label className="fw-semibold text-gray-500 mb-2">Expiration Date <strong className="text-danger">*</strong></label>
                                    <div className="row">
                                        <div className="col-6">
                                            <select className="form-control">
												<option value="1" data-select2-id="select2-data-366-4tvj" defaultValue>Month</option>
												<option value="2" data-select2-id="select2-data-367-m2xs">2</option>
												<option value="3" data-select2-id="select2-data-368-8av3">3</option>
												<option value="4" data-select2-id="select2-data-369-wdmp">4</option>
												<option value="5" data-select2-id="select2-data-370-fyd5">5</option>
												<option value="6" data-select2-id="select2-data-371-9f8g">6</option>
												<option value="7" data-select2-id="select2-data-372-utb6">7</option>
												<option value="8" data-select2-id="select2-data-373-4905">8</option>
												<option value="9" data-select2-id="select2-data-374-i2cc">9</option>
												<option value="10" data-select2-id="select2-data-375-vbtn">10</option>
												<option value="11" data-select2-id="select2-data-376-yj2i">11</option>
												<option value="12" data-select2-id="select2-data-377-il9z">12</option>
											</select>
                                        </div>
                                        <div className="col-6">
                                            <select className="form-control">
												<option data-select2-id="select2-data-27-wjge" defaultValue>Year</option>
												<option value="2023" data-select2-id="select2-data-380-xkn1">2023</option>
												<option value="2024" data-select2-id="select2-data-381-uhkt">2024</option>
												<option value="2025" data-select2-id="select2-data-382-muxw">2025</option>
												<option value="2026" data-select2-id="select2-data-383-3o8b">2026</option>
												<option value="2027" data-select2-id="select2-data-384-uwfw">2027</option>
												<option value="2028" data-select2-id="select2-data-385-qcsq">2028</option>
												<option value="2029" data-select2-id="select2-data-386-17ha">2029</option>
												<option value="2030" data-select2-id="select2-data-387-h3rf">2030</option>
												<option value="2031" data-select2-id="select2-data-388-hy4u">2031</option>
												<option value="2032" data-select2-id="select2-data-389-oln5">2032</option>
												<option value="2033" data-select2-id="select2-data-390-3vc3">2033</option>
											</select>
                                        </div>
                                    </div>
                                </div>
                                <div className="col-sm-4">
                                    <label className="fw-semibold text-gray-500 mb-2">CVV <strong className="text-danger">*</strong></label>
                                    <div className="position-relative">
										<input type="text" className="form-control form-control-solid" minLength="3" maxLength="4" placeholder="CVV" name="card_cvv"/>
										<div className="position-absolute translate-middle-y top-50 end-0 me-3">
											<span className="svg-icon svg-icon-2hx">
												<svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
													<path d="M22 7H2V11H22V7Z" fill="currentColor"></path>
													<path opacity="0.3" d="M21 19H3C2.4 19 2 18.6 2 18V6C2 5.4 2.4 5 3 5H21C21.6 5 22 5.4 22 6V18C22 18.6 21.6 19 21 19ZM14 14C14 13.4 13.6 13 13 13H5C4.4 13 4 13.4 4 14C4 14.6 4.4 15 5 15H13C13.6 15 14 14.6 14 14ZM16 15.5C16 16.3 16.7 17 17.5 17H18.5C19.3 17 20 16.3 20 15.5C20 14.7 19.3 14 18.5 14H17.5C16.7 14 16 14.7 16 15.5Z" fill="currentColor"></path>
												</svg>
											</span>
										</div>
									</div>
                                </div>
                            </div>
                            <div className="d-flex flex-column flex-sm-row my-2">
								<div className="me-5 mb-2">
									<label className="fs-6 fw-bolder form-label my-1">Save Card for further billing?</label>
									<div className="fs-7 text-gray-500">If you need more info, please check budget planning</div>
								</div>
								<label className="form-check form-switch">
									<input className="form-check-input" type="checkbox" value="1" defaultChecked/>
									<span className="form-check-label fw-semibold text-gray-500 text-nowrap">Save Card</span>
								</label>
							</div>
                            <label className="form-check form-switch w-100 justify-content-between">
                                <label className="form-label my-1">Make it Default</label>
                                <input className="form-check-input" type="checkbox" value="1" defaultChecked/>
                            </label>
                        </div>
                    </Modal.Body>
                    <Modal.Footer>
                        <div className="d-flex justify-content-end align-items-center w-100 px-xl-5">
                            <button type="button" className="btn btn-secondary btn-sm btn-min-w mb-2 me-2">Discard</button>
                            <button type="button" className="btn btn-primary btn-sm btn-min-w mb-2">Save Card</button>
                        </div>
                    </Modal.Footer>
                </Modal>

    }
}

export default AddNewCard;